body {
    box-sizing: border-box;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    background-image: linear-gradient(to bottom, rgb(64,58,62,0.4) 10%, rgb(190,88,105, 0.4) 60%);
    background-repeat: no-repeat;
    background-size: 100% 100%;
    background-position: 0, 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding-right: 100px;
}
h1 {
    text-align: center;
    margin-right: 50px;
}
#inputBox {
    width: 300px;
    text-align: center;
    margin-right: 30px;
}

#output {
    box-sizing: border-box;
    margin-left: 50px;
}
#submitButton {
    width: 75px;
}
h3 {
    margin-bottom: -20px;
}

#list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 10px;
    width: 420px;
}
.listItems {
    order: 1;
    margin-top: 0;
}
.emptyDiv {
    order: 2;
    width: 100px;
    flex-grow: 4;
}
.removeButton {
    order: 4;
    margin-left: 20px;
    height: 21px;
}
.checkbox {
    order: 3;
    justify-self: end;
}